import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink
 } from 'reactstrap';
 import "./Styles.css";
import {PlayContext} from '../Providers';


class Layout extends Component {
    constructor(props) {
        super(props);
    
        this.toggle = this.toggle.bind(this);
        this.state = {
          isOpen: false
        };
    }

    static contextType = PlayContext; //React 16.6 support

    componentDidMount() {
    
    }

    componentDidUpdate(prevProps, prevState) {
    }

    toggle() {
      this.setState({
        isOpen: !this.state.isOpen
      });
    }

  render() {
    return (
        <div>
          <Navbar color="dark" dark expand="md">
          <NavbarBrand href="/">PWA-APP</NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink tag={Link} to="/">Home</NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/about">About</NavLink>
              </NavItem>
              <NavItem>
                 <NavLink tag={Link} to="/docs">Docs</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
            <div className="main">
                {this.props.children}
            </div>
            <div>
              <p className="footer text-center">Copyright ©2019 Tim </p>
            </div>
        </div>

    );
  }
}

export default Layout;